/* You can add global styles to this file, and also import other style files */
html {
  /* COLORS */
  --elements-color-01: #000000;
  --elements-color-02: #003c4f;
  --elements-color-03: #333333;
  --elements-color-04: #f4137b;
  --elements-color-05: #ffffff;
  --elements-color-06: "";
  --elements-color-07: "";
  /* FONTS */
  --elements-font-family-primary: "Calibre";
  --elements-font-family-secondary: sans-serif;
  --elements-font-family-primary-alt: "Courier New", Courier, monospace;
  --elements-font-family-secondary-alt: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  --elements-font-size-base: 10px;
  /* CUSTOMIZABLE */
  --elements-title-background-primary: var(--elements-color-01);
  --elements-title-background-secondary: var(--elements-color-02);
  --elements-title-background-primary-alt: var(--elements-color-03);
  --elements-title-background-secondary-alt: var(--elements-color-04);
  --elements-title-font-color-primary: var(--elements-color-04);
  --elements-title-font-color-secondary: var(--elements-color-05);
  --elements-title-font-color-primary-alt: var(--elements-color-02);
  --elements-title-font-color-secondary-alt: var(--elements-color-05);
  --elements-date-background-primary: var(--elements-color-01);
  --elements-date-background-secondary: var(--elements-color-02);
  --elements-date-background-primary-alt: var(--elements-color-03);
  --elements-date-background-secondary-alt: var(--elements-color-04);
  --elements-date-font-color-primary: var(--elements-color-01);
  --elements-date-font-color-secondary: var(--elements-color-05);
  --elements-date-font-color-primary-alt: var(--elements-color-03);
  --elements-date-font-color-secondary-alt: var(--elements-color-04);
  --elements-comment-background-primary: var(--elements-color-02);
  --elements-comment-background-secondary: var(--elements-color-01);
  --elements-comment-background-primary-alt: var(--elements-color-02);
  --elements-comment-background-secondary-alt: var(--elements-color-01);
  --elements-comment-font-color-primary: var(--elements-color-01);
  --elements-comment-font-color-secondary: var(--elements-color-02);
  --elements-comment-font-color-primary-alt: var(--elements-color-03);
  --elements-comment-font-color-secondary-alt: var(--elements-color-04);
  /* textimg */
  --elements-textimg-bg-secondary: var(--elements-color-02);
  --elements-textimg-bg-primary: var(--elements-color-05);
  --elements-textimg-bg-primary-alt: var(--elements-color-07);
  --elements-textimg-bg-secondary-alt: var(--elements-color-03);
  --elements-textimg-font-color-primary: var(--elements-color-01);
  --elements-textimg-font-color-primary-alt: var(--elements-color-02);
  --elements-textimg-font-color-secondary: var(--elements-color-03);
  --elements-textimg-font-color-secondary-alt: var(--elements-color-04);
  /* labelpdf */
  --elements-labelpdf-background-primary: var(--elements-color-02);
  --elements-labelpdf-background-primary-alt: var(--elements-color-03);
  --elements-labelpdf-background-secondary: var(--elements-color-01);
  --elements-labelpdf-background-secondary-alt: var(--elements-color-04);
  --elements-labelpdf-font-color-primary: var(--elements-color-01);
  --elements-labelpdf-font-color-secondary: var(--elements-color-02);
  --elements-labelpdf-font-color-primary-alt: var(--elements-color-03);
  --elements-labelpdf-font-color-secondary-alt: var(--elements-color-04);
  /* namefile*/
  --elements-name-background-primary: var(--elements-color-01);
  --elements-name-background-primary-alt: var(--elements-color-02);
  --elements-name-background-secondary: var(--elements-color-03);
  --elements-name-background-secondary-alt: var(--elements-color-04);
  --elements-name-font-color-primary: var(--elements-color-01);
  --elements-name-font-color-primary-alt: var(--elements-color-02);
  --elements-name-font-color-secondary: var(--elements-color-03);
  --elements-name-font-color-secondary-alt: var(--elements-color-04);
  /* SPINNER */
  --elements-color-spinner: var(--elements-color-01);
  /* DATA ERROR */
  --elements-color-error-text: var(--elements-color-04);
  --elements-border-error: 1px solid var(--elements-color-04);
  /* BUTTONS */
  --elements-button-bg-color-primary: var(--elements-color-01);
  --elements-button-font-color-primary: var(--elements-color-05);
  --elements-button-bg-color-secondary: var(--elements-color-05);
  --elements-button-font-color-secondary: var(--elements-color-01);
  --elements-button-font-family: var(--elements-font-family-primary);
  /* MARKET COMMENT */
  --elements-mc-font-family: var(--elements-font-family-primary);
  --elements-mc-bg-color-title: var(--elements-color-01);
  --elements-mc-bg-color-container: var(--elements-color-05);
  --elements-mc-color-title: var(--elements-color-05);
  --elements-mc-color-date: var(--elements-color-01);
  --elements-mc-color-text: var(--elements-color-01);
  --elements-mc-border-size: 2px;
  --elements-mc-border-color: var(--elements-color-01);
  --elements-mc-color-moreinfo: var(--elements-color-04);
  --elements-mc-font-size-title: 2em;
  --elements-mc-font-size-date: 1.7em;
  --elements-mc-font-size-comment: 1.6em;
  --elements-mc-font-size-moreinfo: 1.6em;
  --elements-mc-font-weight: 700;
  --elements-mc-font-weight-date: 300;
  --elements-mc-font-weight-comment: 300;
  /* CALENDAR WEEK */
  --elements-cw-bg-color-title: var(--elements-color-01);
  --elements-cw-color-title: var(--elements-color-05);
  --elements-cw-color-text-img: var(--elements-color-05);
  --elements-cw-color-date: var(--elements-color-01);
  --elements-cw-bg-color-row-hover: #f4feff;
  --elements-cw-separator-color: #dfdfdf;
  --elements-cw-font-size-title: 2em;
  --elements-cw-font-size-text-img: 3em;
  --elements-cw-font-size-date: 1.6em;
  --elements-cw-border-size: 2px;
  --elements-cw-border-color: var(--elements-color-01);
  --elements-cw-font-weight-title: 700;
  --elements-cw-font-weight-text-img: 700;
  --elements-cw-font-weight-date: 400;
  /* CALENDAR MONTH */
  --elements-cm-bg-color-title: var(--elements-color-01);
  --elements-cm-color-title: var(--elements-color-05);
  --elements-cm-font-size-title: 2em;
  --elements-cm-font-size-text-img: 3em;
  --elements-cm-color-text-img: var(--elements-color-05);
  --elements-cm-border-size: 2px;
  --elements-cm-border-color: var(--elements-color-01);
  --elements-cm-bg-card: var(--elements-color-05);
  --elements-cm-font-size-date: 1.6em;
  --elements-cm-font-weight-title: 700;
  --elements-cm-font-weight-text-img: 700;
  --elements-cm-font-weight-date: 400;
  --elements-cm-color-date: var(--elements-color-02);
  --elements-cm-not-found-label-font-color-primary: var(--elements-color-02);
  --elements-cm-not-found-label-font-color-primary-alt: var(--elements-color-01);
  --elements-cm-not-found-label-font-color-secondary: var(--elements-color-03);
  --elements-cm-not-found-label-font-color-secondary-alt: var(--elements-color-07);
  --elements-cm-notfound-font-size: 1.4em;
  --elements-cm-notfound-color: var(--elements-color-04);
  --elements-cm-notfound-font-weight: 500;
  --elements-cm-not-found-label-background-primary: var(--elements-color-02);
  --elements-cm-not-found-label-background-primary-alt: var(--elements-color-01);
  --elements-cm-not-found-label-background-secondary: var(--elements-color-03);
  --elements-cm-not-found-label-background-secondary-alt: var(--elements-color-07);
  /* BANNER & ETF */
  --elements-tfs-bg-color-title: var(--elements-color-01);
  --elements-tfs-color-title: var(--elements-color-05);
  --elements-tfs-title-font-size-title: 2em;
  --elements-tfs-title-font-size-comment: 1.6em;
  --elements-tfs-color-comment: var(--elements-color-01);
  --elements-tfs-bg-color-comment: var(--elements-color-05);
  --elements-tfs-weight-title: 700;
  --elements-tfs-weight-comment: 300;
  --elements-tfs-border-color: var(--elements-color-01);
  --elements-tfs-border-size: 2px;
  /* CUSTOM BANNER */
  --elements-bn-color-title: var(--elements-color-01);
  --elements-bn-font-size-title: 1.4em;
  --elements-bn-weight-title: bold;
  --elements-bn-font-size-subtitle: 1.4em;
  --elements-bn-bg-color-subtitle: var(--elements-color-01);
  --elements-bn-weight-subtitle: normal;
  --elements-bn-border-color: var(--elements-color-01);
  --elements-bn-border-size: 2px;
  /* DOWNLOAD LINK */
  --elements-dw-color-filename: var(--elements-color-01);
  --elements-dw-color-filesize: var(--elements-color-04);
  --elements-dw-font-size-filename: 1.4em;
  --elements-dw-font-size-filesize: 1.4em;
  --elements-dw-font-weight-filename: 500;
  --elements-dw-font-weight-filesize: 500;
}

